    font-size: 1.4rem;
  }
}

@keyframes scrollLineMove {
  0% {
    transform: translateY(0);
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    transform: translateY(2rem);
    opacity: 0;
    @media screen and (min-width: 768px) {
      transform: translateY(3rem);
    }
  }
}
@media screen and (max-width: 767px) {
  @keyframes scrollLineMove {
    0% {
      transform: translateY(0);
      opacity: 0;
    }
    20% {
      opacity: 1;
    }
    80% {
      opacity: 1;
    }
    100% {
      transform: translateY(2rem);
      opacity: 0;
    }
  }
}
@media screen and (min-width: 768px) {
